.highlight {
  position: relative;
  margin-top: 5px;
  padding: 8px 15px;
  border-left: 3px solid; }
  .highlight.info {
    background-color: rgba(0, 153, 255, 0.08) !important;
    border-color: #09f !important; }
  .highlight.success {
    background-color: rgba(102, 204, 0, 0.1) !important;
    border-color: #6c0 !important; }
  .highlight.tips {
    background-color: rgba(255, 255, 0, 0.15) !important;
    border-color: #fd0 !important; }
  .highlight.warning {
    background-color: rgba(255, 170, 0, 0.1) !important;
    border-color: #fa0 !important; }
  .highlight.error {
    background-color: rgba(255, 51, 51, 0.08) !important;
    border-color: #f33 !important; }
  .highlight .v-icon {
    position: absolute;
    left: -9px;
    color: #fff !important;
    border-radius: 1em;
    width: 15px;
    height: 15px; }
  .highlight.info .v-icon {
    background: #09f !important;
    -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
            transform: rotate(180deg);
    font-size: 23px;
    top: 0.4em; }
  .highlight.success .v-icon {
    background-color: #6c0 !important;
    font-size: 15px;
    top: 0.7em; }
  .highlight.error .v-icon {
    background-color: #f33 !important;
    font-size: 15px;
    top: 0.7em; }
  .highlight.tips .v-icon {
    background-color: #fd0 !important;
    font-size: 14px;
    top: 0.7em;
    -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
            transform: rotate(180deg); }
  .highlight.warning .v-icon {
    background-color: #fa0 !important;
    top: 0.4em; }



