.features {
  line-height: 1.8;
  padding-left: 2.2em;
  position: relative; }
  .features .v-icon {
    line-height: 0.8;
    position: absolute;
    left: 0;
    padding-top: 0.1em; }

.coming-soon {
  font-style: italic; }
  .coming-soon .v-card__title {
    font-size: 1.2em; }
  .coming-soon .v-chip {
    margin: 2px 0; }

