  font-size: 1.1em;
  margin-right: 10px;
  margin-bottom: 10px; }

.code {
  font-family: monospace,monospace; }

.pulse {
  -webkit-animation: pulse 1.5s infinite;
          animation: pulse 1.5s infinite; }



